import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup
	},
	data() {
		return {
			title: '修改客户&供应商',
			reqData: {
				type: 0,
				company: '',
				contacts: '',
				mobile: '',
				address: [],
				street: '',
				email: '',
				remarks: ''
			},
			disableSubmit: true
		}
	},
	onLoad(options) {
		this.reqData.type = options.type
		this.reqData.company = options.company
		this.reqData.contacts = options.contacts
		this.reqData.mobile = options.mobile
		this.reqData.address = options.address.split(',')
		this.reqData.street = options.street
		this.reqData.email = options.email
		this.reqData.remarks = options.remarks
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleTypeChange(val) {
			this.reqData.type = val.detail.value
		},
		handleAddressChange(val) {
			if (val.detail.value) {
				this.reqData.address = []
				for (let item of val.detail.value) {
					this.reqData.address.push(item)
				}
			}
		},
		handleSubmit() {
			uni.navigateBack()
		}
	},
	watch:{
		reqData: {
			handler(val) {
				if (val.company && val.contacts && val.mobile && val.address && val.street) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
